.student__profile{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 15px 15px;
    border-radius: 30px;
    padding-bottom: 30px;
}

.search{
    display: flex;
    height: 50px;
    width: 400px;
    margin: 10px auto;
    border-radius: 3rem;
    box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -webkit-box-shadow: -1px 7px 48px -8px #0000008f;
    -moz-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    transition: 0.4s ease-in-out;
    
}

.search:hover{
    cursor: pointer;
  
}

.input__fld{
    width: 80%;
    height: 100%;
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 20px;
    /* background:red; */
}

.input__fld>input{
   overflow-y: hidden;
   width: 100%;
   height: 30px;
   border: none;
   border-bottom: 1px solid gray;
   outline:none;
   font-size:20px;
   /* font-family: 'Courgette', cursive; */
   letter-spacing: 1.5px;
   background-color: transparent;
}


.icon__fld{
    width: 20%;
