.left__side{
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    width:25%;
    height:84vh;
    transition: all 2s ease-in-out;
}

.right__side{
    width:73%;
    height:84vh;
    border-radius:50px;
    border-radius: 30px;
    /* box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -webkit-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -moz-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56); */
    transition: all 2s ease-in-out;

}

.top{
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-evenly;
    width: 100%;
    height: 65%;
    border-radius: 50px;
    box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -webkit-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -moz-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
}
.bottom{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 30%;
    border-radius: 50px;
    box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -webkit-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
    -moz-box-shadow: -1px 7px 48px -8px rgba(0,0,0,0.56);
}

.bottom>div{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 65px;
    height: 65px;
    border-radius: 50px;
    cursor: pointer;
    transition: all .4s ease-in-out;
}
.active{
    box-shadow:-1px 7px 48px -8px rgba(0,0,0,0.56);
    transition: all .4s ease-in-out;
}

