.view{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    padding: 20px 20px;
}

.view>div{
    /* background-color: red; */
    border-radius: 15px;
    padding-left: 20px;
    display: flex;
    align-items: center;
    width: 45%;
    height: 40px;
    box-shadow: 2px 7px 30px -7px rgba(0,0,0,0.50);
    -webkit-box-shadow: -1px 7px 30px -8px rgba(0,0,0,0.50);
    -moz-box-shadow: -1px 7px 30px -8px rgba(0,0,0,0.50);
    font-weight: bold;
}

.view>div:last-child{
    /* background-color: red; */
    width: 100%;
    min-height: 60px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    line-height: 20px;
}

.view>div>span{
    margin-left: 5px;
    font-weight: normal;
}
.semester{
    overflow-y: scroll;    
    width: 100%;
    height: 84vh;
    /* background-color: brown; */
    padding: 0 20px;
    
}
.semester::-webkit-scrollbar
{
	width: 6px;
}
.semester::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: lightgray;
}

.nav__bar{
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 50px;
    width:150px;
    height: 50px;
    margin-bottom: -20px;
}

.nav__bar>div{
    width: 50px;
    height: 30px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin:0 10px;
	box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    cursor: pointer;
}
.nav__bar>div:nth-child(2){
    padding-top:5.5px ;
    width: 35px;
    height: 35px;
    border-radius: 50%;
	box-shadow: inset 0 0 4px rgba(0,0,0,.3);
}

